/* eslint-disable import/no-anonymous-default-export */
import React from 'react'
import ComFilhos from './components/ComFilhos';
import ComParametro from './components/ComParametro';
import Card from './components/layout/Card';
import Header from './components/Header';
import ListaPresentes from './components/ListaPresentes';
import NavBar from './components/NavBar/NavBar';
import "./App.css"


export default props => (
  <div className="App">
    <NavBar />
    <Header></Header>
    <div>
    O totem da raposa é universal, é um animal que sobrevive no deserto, no ártico e em qualquer habitat da terra, o que o torna famoso pela sua adaptabilidade ao meio ambiente. A raposa vive em qualquer lugar do mundo desde tempos imemoriais e faz parte do folclore e da filosofia de todos os continentes.

A sabedoria e astúcia da raposa incluem: Mudança de forma, Destreza, Habilidades de observação, Disfarce, Camuflagem, Valor feminino, Invisibilidade, Capacidade de observar o que aos outros passa despercebido, Persistência, Gentileza e Velocidade. 

A raposa simboliza não apenas a astúcia dada pela inteligência, mas também a capacidade de encontrar soluções para novos problemas que surgem. Se nenhum animal encontrar uma maneira de cruzar o rio, a raposa encontrará um tronco e o empurrará, ou encontrará um atalho.

Além disso, a raposa ensina-nos a capacidade de nos tornarmos um com o meio ambiente. Finalmente, deve-se notar que a raposa é considerada a protetora da unidade familiar, principalmente na adversidade ou nos maus momentos, já que a raposa domina o caminho para sobreviver mesmo nos invernos mais rigorosos.

Na China, o simbolismo da raposa gira em torno da vida após a morte. A crença popular diz que o avistamento de uma raposa é um sinal do espírito dos falecidos.

No simbolismo celta, a raposa era um guia e era reconhecida pela sua sabedoria. Os celtas dizem que a raposa conhece as florestas intimamente, e é por isso que seria um grande guia no mundo espiritual.

No Japão, a raposa é considerada um dos espíritos da chuva, e um mensageiro do Inari, o deus do arroz. No Japão a raposa também simboliza a longevidade e a proteção contra o mal. No entanto, na China, diz-se que os espíritos das raposas mantêm os homens longe de suas esposas, e é por isso que chamam as amantes dos seus maridos de suas "raposas" .

Na tradição nativa americana, o simbolismo da raposa tem duas interpretações. Nas tribos do norte a raposa era vista como um mensageiro inteligente e nobre. Em contraste, nas tribos das pradarias, a raposa é considerada uma trapaceira, que gasta piadas pesadas ou, pior ainda, atrai a morte.
Mas, em geral, todas as culturas concordam em relacionar a raposa com astúcia, inteligência, pensamento ágil, sagacidade e sabedoria.

A raposa totémica encoraja-nos a pensar fora da caixa, a estimular a inteligência e a usar a nossa inteligência de uma maneira diferente e criativa. A raposa também nos traz a mensagem de tentar ver as nossas circunstâncias de um modo diferente do habitual. A raposa diz para estarmos cientes de alguns dos nossos hábitos e tentar outro ponto de vista da ação.

A raposa também é um lembrete de que devemos usar todos os nossos recursos (visíveis e invisíveis) para alcançar os nossos objetivos. Por vezes isto significa utilizar alguns métodos não ortodoxos.

Por outro lado, a raposa é um sinal de estar ciente do nosso redor.

A raposa é extraordinariamente eficaz e adaptável, a raposa muda incrivelmente a sua performance, adaptando-se sempre às novas circunstâncias. A raposa totémica diz-nos para não reclamar, mas sim adaptar-nos ao nosso ambiente, a raposa funde-se com ele, e é assim com o meio ambiente (e as circunstâncias) colocando-os a seu favor. 

A astúcia, a engenhosidade, a concentração nos objetivos importantes da vida e a adaptação ao ambiente é o que o totem da raposa traz para aquele que o invoca.

Se a criatividade e a engenhosidade forem necessárias, é provável que o totem da raposa apareça. A raposa é um totem ideal para ter sucesso e desenvolvimento na vida.
    </div>
    {/* <div className="Itens">
      <ListaPresentes></ListaPresentes>
    </div> */}
  </div>
)